/* Styles pour la barre de dfilement */
/* Barre de dfilement globale */
::-webkit-scrollbar {
  width: 10px; /* Largeur de la barre de dfilement */
}

/* Barre de dfilement track (piste) */
::-webkit-scrollbar-track {
  background: #bfc0d3; /* Couleur de fond de la piste */
}

/* Poigne de la barre de dfilement (thumb) */
::-webkit-scrollbar-thumb {
  background: #f19088; /* Couleur de la poigne */
}

/* Styles pour la poigne de la barre de dfilement au survol */
/* Vous pouvez personnaliser ces styles si ncessaire */
::-webkit-scrollbar-thumb:hover {
  background: #ff5733; /* Couleur de la poigne au survol */
}

/* Styles pour la barre de dfilement boutons (flches) si elles sont visibles */
/* Vous pouvez personnaliser ces styles si ncessaire */
::-webkit-scrollbar-button {
  display: none; /* Pour masquer les boutons */
}

/* Styles pour la barre de dfilement de coin (corner) */
/* Vous pouvez personnaliser ces styles si ncessaire */
::-webkit-scrollbar-corner {
  background: transparent; /* Couleur du coin */
}
